#photo-container {
  position: relative;
  width: 100%;
  overflow: hidden;
}
#photo-container .alum {
  display: inline-block;
  vertical-align: top;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  width: 100%;
}
#photo-container .alum.alum-open {
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}
#photo-container .alum-expand {
  display: inline-block;
  vertical-align: top;
  position: absolute;
  width: 100%;
  left: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
#photo-container .alum-expand.alum-open {
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}
#photo-container .alum-expand .alum-expand-head {
  position: absolute;
  right: 20px;
}
#photo-container .alum-expand .alum-expand-body {
  width: 80%;
  margin: auto;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo {
  display: inline-block;
  position: relative;
  margin: 10px 10px;
  height: 300px;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(143, 143, 143, 0.5);
  visibility: hidden;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo span {
  position: absolute;
  top: 45%;
  left: 45%;
  color: white;
  font-weight: 700;
  visibility: hidden;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo img {
  height: 100%;
  max-width: 100%;
  vertical-align: middle;
  border-radius: 5px;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo:hover {
  cursor: pointer;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo:hover span {
  visibility: visible;
}
#photo-container .alum-expand .alum-expand-body .alum-single-photo:hover:after {
  visibility: visible;
}
#photo-container .single-alum {
  display: inline-block;
  width: 20%;
  margin: 15px 2%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
#photo-container .ant-card .ant-card-body .ant-card-meta {
  text-align: center;
}
#photo-container .ant-card .ant-card-body .ant-card-meta .ant-card-meta-title {
  font-weight: 700;
}
#photo-container .ant-card .ant-card-body .ant-card-meta .ant-card-meta-description {
  text-align: center;
}



