#log-container {
  height: 100vh;
  width: 100vw;
  position: relative;
  background: #AEc4d2;
}
#login-panel {
  height: 600px;
  width: 400px;
  padding: 30px;
  background: white;
  color: black;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -300px;
  margin-left: -200px;
  -webkit-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.25);
}
#login-panel h1 {
  margin-left: 5%;
  color: #6b92aa;
}
#login-panel h2 {
  margin: 80px 0;
  margin-left: 5%;
}
#login-panel h2 em {
  display: block;
  color: #999999;
}
#login-panel .ant-input {
  border: none;
  box-shadow: none;
  -webkit-box-shadow: none;
}
#login-panel #login-name,
#login-panel #login-password {
  display: block;
  border: none;
  width: 80%;
  margin-left: 5%;
  font-size: 16px;
  margin-top: 10px;
}
#login-panel svg {
  position: absolute;
  top: 25px;
  left: 0;
  pointer-events: none;
}
#login-panel .sign-in-button {
  color: #AEc4d2;
  font-size: 30px;
  margin-left: 5%;
  margin-top: 30px;
  font-weight: 500;
}
#login-panel .sign-in-button:hover {
  font-size: 35px;
  cursor: pointer;
}
