.fun-container {
  min-height: 100vh;
  padding: 30px;
}
.fun-home-container {
  margin: 30px 0 0 0;
  padding: 20px;
  border: 1px solid #e8e8e8;
}
.fun-home-container .left {
  display: inline-block;
  vertical-align: top;
  width: 50%;
}
.fun-home-container .text {
  border: 1px solid #e8e8e8;
}
.fun-home-container .text .ant-input-search {
  margin-bottom: 15px;
}
.fun-home-container .text .text-container {
  padding: 10px;
}
.fun-home-container .text .button-container {
  text-align: center;
}
.fun-home-container .text .button-container button:first-child {
  margin-right: 15px;
}
.fun-home-container .preview {
  border: 1px solid #e8e8e8;
  padding: 10px;
}
.fun-home-container .preview.preview-phone {
  display: inline-block;
  vertical-align: top;
  width: 49%;
  margin-left: 1%;
  height: 770px;
}
.fun-home-container .preview.preview-phone .pre-continer {
  margin: auto;
  width: 400px;
  height: 700px;
}
.fun-home-container .preview.preview-phone .pre-continer h3 {
  padding-top: 300px;
}
.fun-home-container .preview.preview-pc {
  margin: 20px 0;
  height: 391px;
}
.fun-home-container .preview.preview-pc .pre-continer {
  height: 330px;
}
.fun-home-container .preview.preview-pc .pre-continer h3 {
  padding-top: 150px;
}
.fun-home-container .preview .pre-continer {
  background: black url(../../images/home/home-bg-small.jpg) no-repeat center center;
  background-size: cover;
  border-radius: 10px;
  position: relative;
  text-align: center;
  vertical-align: middle;
  color: white;
}
.fun-home-container .preview .pre-continer h3 {
  color: white;
}
.fun-home-container .preview .pre-continer ul {
  list-style: none;
}
.fun-log-container .editor-tabs {
  position: relative;
  overflow: hidden;
  min-height: 800px;
  width: 1100px;
  margin: auto;
}
.fun-log-container .editor-tabs .tab {
  width: 100%;
  position: absolute;
  -webkit-transition: ease-in-out 1s;
  -o-transition: ease-in-out 1s;
  transition: ease-in-out 1s;
}
.fun-log-container .editor-tabs .bf-container {
  border: 1px solid #e8e8e8;
}
.fun-log-container .editor-tabs .pre-component {
  width: 50%;
  margin: 60px auto 0;
  border: 1px solid #e8e8e8;
  padding: 20px;
}
.fun-log-container .editor-tabs .pre-component h3 {
  margin: 20px 0 10px 0;
}
.fun-log-container .editor-tabs .pre-component .pic-uploader {
  width: 100%;
  text-align: center;
}
.fun-log-container .editor-tabs .pre-component .pic-uploader .ant-upload-select-picture-card {
  min-width: 104px;
  width: auto;
}
.fun-log-container .editor-tabs .pre-component .pic-uploader img {
  max-width: 100%;
  max-height: 440px;
}
@media screen and (max-width: 1050px) {
  .fun-home-container .left {
    width: 100%;
  }
  .fun-home-container .preview.preview-phone {
    margin-left: 0;
    width: 100%;
  }
}
@media screen and (max-width: 700px) {
  .fun-container {
    padding: 0;
  }
  .fun-home-container .preview.preview-phone {
    height: 580px;
  }
  .fun-home-container .preview.preview-phone .pre-continer {
    width: 80%;
    height: 500px;
  }
  .fun-home-container .preview.preview-phone .pre-continer h3 {
    padding-top: 200px;
  }
  .fun-home-container .preview.preview-pc {
    height: 291px;
  }
  .fun-home-container .preview.preview-pc .pre-continer {
    height: 230px;
  }
  .fun-home-container .preview.preview-pc .pre-continer h3 {
    padding-top: 100px;
  }
}



