@font-settings {
  font-family: "Gilroy ExtraBold", system-ui, sans-serif;
  font-style: normal;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-font-kerning: normal;
  -webkit-text-size-adjust: 100%;
}
#menu-check {
  position: fixed;
  top: 30px;
  right: 50px;
  width: 30px;
  height: 40px;
  opacity: 0;
  z-index: 1001;
}
.hamburger {
  z-index: 1001;
  position: fixed;
  pointer-events: none;
  top: 50px;
  right: 50px;
  width: 30px;
  height: 2px;
  background: #000;
  display: block;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.hamburger:after,
.hamburger:before {
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #000;
}
.hamburger:before {
  top: -10px;
}
.hamburger:after {
  bottom: -10px;
}
.menu ul {
  list-style: none;
  width: 100%;
  margin-top: 20vh;
}
.menu .menu-body {
  position: fixed;
  z-index: 1000;
  width: 50vw;
  height: 100vh;
  background: white;
  top: 0;
  right: -50vw;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  text-align: center;
  color: black;
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 3rem;
  display: inline-block;
  vertical-align: middle;
}
.menu .menu-body ul li {
  margin: 1rem 0;
  display: block;
  width: 100%;
  position: relative;
  overflow: hidden;
  text-align: left;
  padding-left: 35%;
  -webkit-transition: padding 0.3s ease-in-out;
  -o-transition: padding 0.3s ease-in-out;
  transition: padding 0.3s ease-in-out;
}
.menu .menu-body ul li a {
  color: black;
}
.menu .menu-body ul li span {
  position: absolute;
  left: 20%;
  top: 2.2rem;
  -webkit-transition: top 0.3s ease-in-out;
  -o-transition: top 0.3s ease-in-out;
  transition: top 0.3s ease-in-out;
}
.menu .menu-body ul li:hover {
  padding-left: 40%;
  cursor: pointer;
}
.menu .menu-body ul li:hover span {
  top: 0;
}
.menu .menu-label {
  position: fixed;
  z-index: 1000;
  width: 50vw;
  height: 100vh;
  background: black;
  top: 0;
  left: -50vw;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  text-align: center;
  color: white;
}
.menu .menu-label .container {
  margin-top: 20vh;
  height: 29rem;
  overflow: hidden;
  font-size: 1.8rem;
  position: relative;
}
.menu .menu-label .container ul {
  margin: 0;
  position: absolute;
  top: 0;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  padding-left: 0;
}
.menu .menu-label .container ul li {
  display: block;
  height: 29rem;
}
.menu .menu-label .container ul li #nav-home-level {
  height: 100%;
  background: url(../../images/home/led-long-2.png) no-repeat;
  line-height: 29rem;
  background-position: 30% center;
  background-size: 100px 100px;
}
.menu .menu-label .container ul li #nav-log-level span {
  display: inline-block;
  height: 100%;
  -webkit-animation: stretch 2.5s cubic-bezier(0.4, 1.4, 0.75, 0.9) infinite;
          animation: stretch 2.5s cubic-bezier(0.4, 1.4, 0.75, 0.9) infinite;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  font-size: 50px;
  line-height: 25rem;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(10) {
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(9) {
  -webkit-animation-delay: 2.25s;
          animation-delay: 2.25s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(8) {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(7) {
  -webkit-animation-delay: 1.75s;
          animation-delay: 1.75s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(6) {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(5) {
  -webkit-animation-delay: 1.25s;
          animation-delay: 1.25s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(4) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(3) {
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(2) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.menu .menu-label .container ul li #nav-log-level span:nth-child(1) {
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}
.menu .menu-label .container ul li #nav-photo-level {
  height: 100%;
  position: relative;
}
.menu .menu-label .container ul li #nav-photo-level span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.menu .menu-label .container ul li #nav-photo-level span:nth-child(1) {
  display: block;
  height: 100%;
  background: url(../../images/home/photo-lebel-1.jpg) no-repeat;
  background-position: center center;
  background-size: auto 100%;
  -webkit-animation: picturn 8s ease-in-out infinite;
          animation: picturn 8s ease-in-out infinite;
}
.menu .menu-label .container ul li #nav-photo-level span:nth-child(2) {
  display: block;
  opacity: 0;
  height: 100%;
  background: url(../../images/home/photo-lebel-2.jpg) no-repeat;
  background-position: center center;
  background-size: auto 100%;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}
input:checked + .menu-button {
  border-radius: 0;
}
input:checked + .menu-button .hamburger {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
input:checked + .menu-button .hamburger:after {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  bottom: 0;
}
input:checked + .menu-button .hamburger:before {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  top: 0;
}
input:checked + .menu-button + .menu .menu-body {
  right: 0;
}
input:checked + .menu-button + .menu .menu-label {
  left: 0;
}
@media screen and (max-width: 600px) {
  #menu-check {
    top: 15px;
    right: 20px;
    opacity: 0;
  }
  .hamburger {
    background: #fff;
    top: 35px;
    right: 20px;
  }
  .hamburger:after,
  .hamburger:before {
    background: #fff;
  }
  .menu ul {
    margin-top: 0;
  }
  .menu .menu-body {
    width: 100vw;
    height: 70vh;
    left: 0;
    top: 100vh;
    -webkit-transition-delay: 0.8s;
         -o-transition-delay: 0.8s;
            transition-delay: 0.8s;
    font-size: 1.2rem;
  }
  .menu .menu-body ul li {
    margin: 2% 0;
  }
  .menu .menu-body ul li span {
    top: 0;
  }
  .menu .menu-label {
    width: 100vw;
    height: 30vh;
    left: 0vw;
    top: -30vh;
    -webkit-transition-delay: 0.8s;
         -o-transition-delay: 0.8s;
            transition-delay: 0.8s;
  }
  .menu .menu-label .container {
    margin-top: 0;
    margin: 0 auto;
    height: 100%;
    width: 150px;
    font-size: 1.2rem;
  }
  .menu .menu-label .container ul li {
    width: 150px;
    height: 30vh;
    position: absolute;
  }
  .menu .menu-label .container ul li:nth-child(1) {
    left: 0;
  }
  .menu .menu-label .container ul li:nth-child(1) #nav-home-level {
    color: transparent;
    line-height: 40vh;
    background-size: 80px 80px;
    background-position: center center;
  }
  .menu .menu-label .container ul li:nth-child(2) {
    left: 150px;
  }
  .menu .menu-label .container ul li:nth-child(2) #nav-log-level {
    width: 100%;
    overflow: hidden;
  }
  .menu .menu-label .container ul li:nth-child(2) #nav-log-level span {
    font-size: 1.3rem;
    line-height: 30vh;
  }
  .menu .menu-label .container ul li:nth-child(3) {
    left: 300px;
  }
  .menu .menu-label .container ul li:nth-child(4) {
    left: 450px;
  }
  .menu .menu-label .container ul li:nth-child(5) {
    left: 600px;
  }
  .menu .menu-label .container ul li:nth-child(6) {
    left: 750px;
  }
  .menu .menu-label .container ul li:nth-child(7) {
    left: 900px;
  }
  input:checked + .menu-button + .menu .menu-body {
    -webkit-animation: 1.5s up-no-delay;
            animation: 1.5s up-no-delay;
    top: 30vh;
  }
  input:checked + .menu-button + .menu .menu-label {
    -webkit-animation: 1.5s down-no-delay;
            animation: 1.5s down-no-delay;
    top: 0;
  }
}
@-webkit-keyframes stretch {
  5% {
    -webkit-transform: scaleX(500);
            transform: scaleX(500);
    opacity: .1;
  }
  15% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    opacity: 1;
  }
}
@keyframes stretch {
  5% {
    -webkit-transform: scaleX(500);
            transform: scaleX(500);
    opacity: .1;
  }
  15% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    opacity: 1;
  }
}
@-webkit-keyframes picturn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes picturn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes up-no-delay {
  0% {
    top: 100vh;
  }
  100% {
    top: 30vh;
  }
}
@keyframes up-no-delay {
  0% {
    top: 100vh;
  }
  100% {
    top: 30vh;
  }
}
@-webkit-keyframes down-no-delay {
  0% {
    top: -30vh;
  }
  100% {
    top: 0;
  }
}
@keyframes down-no-delay {
  0% {
    top: -30vh;
  }
  100% {
    top: 0;
  }
}



