.log-container {
  position: relative;
  padding-bottom: 20px;
  width: 100vw;
  overflow: hidden;
}
.log-container embed {
  width: 100%;
}
.log-container .log-list {
  overflow-y: scroll;
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  z-index: 102;
  -webkit-transition: 1.5s;
  -o-transition: 1.5s;
  transition: 1.5s;
}
.log-container .log-list embed {
  background: white;
}
.log-container .log-list.log-list-close {
  pointer-events: none;
  -webkit-transform: translateY(-100vh);
      -ms-transform: translateY(-100vh);
          transform: translateY(-100vh);
}
.tmlog {
  -webkit-transition: all 1.5s;
  -o-transition: all 1.5s;
  transition: all 1.5s;
  border: 1px solid #e8e8e8;
  border-bottom: none;
  background: white;
  padding: 0 10%;
  position: relative;
}
.tmlog:hover {
  cursor: pointer;
}
.tmlog:hover h2 {
  font-size: 28px;
  color: #1dB0B8;
}
.tmlog:last-child {
  border-bottom: 1px solid #e8e8e8;
}
.tmlog .log-tags-icon {
  padding: 20px;
  padding-top: 50px;
  display: inline-block;
  vertical-align: middle;
  width: 100px;
  height: 100px;
  margin-right: 20px;
}
.tmlog .log-label {
  display: inline-block;
  vertical-align: top;
  width: 80%;
  padding: 20px;
  border-radius: 20px;
  overflow: hidden;
}
.tmlog .log-label .log-time span {
  margin-right: 20px;
}
.tmlog .log-label .log-time span:nth-child(1) {
  color: gray;
}
.tmlog .log-label .log-time span:nth-child(2) {
  color: #1dB0B8;
}
.tmlog .log-label .log-time span:nth-child(3) {
  float: right;
  visibility: hidden;
}
.tmlog .log-label h2 {
  font-size: 26px;
  position: relative;
}
.tmlog .log-label p {
  font-size: 18px;
  text-indent: 2rem;
}
.open-log {
  width: 1000px;
  margin: auto;
}
.open-log .log-bg {
  background: center top;
  background-size: cover;
  height: 400px;
  position: relative;
  margin-left: -50px;
  margin-right: -50px;
  top: 0;
  margin-bottom: 80px;
}
.open-log .log-bg .head {
  color: white;
  position: absolute;
  top: 0;
  width: 100%;
  padding: 30px 55px 30px 55px;
  text-align: center;
}
.open-log .log-bg .head span {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  font-size: 30px;
  line-height: 40px;
  border-radius: 20px;
}
.open-log .log-bg .head span:first-child {
  float: left;
}
.open-log .log-bg .head span:last-child {
  float: right;
}
.open-log .log-bg .head span:hover {
  cursor: pointer;
}
.open-log .log-bg .head span i {
  background: rgba(172, 170, 170, 0.5);
  padding: 10px 15px;
  border-radius: 15px;
}
.open-log .log-bg .head embed {
  height: 40px;
}
.open-log .log-bg .zhezhao {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
}
.open-log .log-bg .bottom {
  position: absolute;
  bottom: 110px;
  margin: 0 55px;
  color: white;
  font-size: 20px;
}
.open-log .log-bg .bottom i {
  font-size: 30px;
  color: white;
  background: rgba(172, 170, 170, 0.5);
  padding: 10px;
  vertical-align: middle;
  border-radius: 50%;
}
.open-log .log-bg .bottom i:hover {
  cursor: pointer;
}
.open-log .log-title {
  margin: 30px 0;
  font-size: 36px;
  font-weight: 700;
  text-align: center;
}
.open-log img {
  max-width: 100%;
}
.open-log .log-label {
  margin-top: 110px;
  padding: 20px 30px 40px 30px;
  position: relative;
  top: -200px;
  background: white;
  border-radius: 15px;
  z-index: 100;
}
.open-log .log-label h2 {
  text-align: center;
  font-weight: 700;
}
.open-log .log-label pre {
  color: white;
  background-color: #3E3E3E;
}
.open-log .log-label .desc {
  padding: 20px;
  border-bottom: 1px solid #e8e8e8;
  font-weight: 700;
  font-size: 18px;
}
.open-log .log-label .desc img {
  width: 64px;
  height: 64px;
  display: inline-block;
  border-radius: 15px;
  margin-right: 30px;
  -webkit-box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
}
.open-log .log-label .desc span {
  background: rgba(238, 132, 62, 0.5);
  padding: 2px 5px;
  border-radius: 10px;
}
.open-log .log-label .desc ul {
  display: inline-block;
  list-style: none;
  position: relative;
  top: 12px;
}
@media screen and (max-width: 1100px) {
  .open-log {
    width: 90%;
  }
  .open-log .log-bg {
    width: 100vw;
  }
  .open-log .log-bg .head span {
    font-size: 20px;
    line-height: 30px;
  }
  .open-log .log-bg .head span i {
    padding: 5px 10px;
    border-radius: 10px;
  }
  .open-log .log-bg .head embed {
    width: auto;
    height: 30px;
  }
  .open-log .log-label .desc {
    padding: 20px 0;
  }
  .open-log .log-label .desc img {
    margin-right: 5px;
  }
  .open-log .log-label .desc ul {
    padding: 0;
  }
  .open-log .log-label .desc ul li {
    display: none;
  }
  .open-log .log-label .desc ul li:last-child {
    display: inline;
  }
}
@media screen and (max-width: 1000px) {
  .tmlog {
    width: 100%;
    padding: 0;
  }
  .tmlog .log-tags-icon {
    display: none;
  }
  .tmlog .log-label {
    padding: 5%;
    width: 100%;
  }
  .tmlog .log-label .log-time span:nth-child(3) {
    visibility: visible;
  }
}
@media screen and (max-width: 800px) {
  .open-log .log-bg {
    height: 300px;
    width: 121%;
  }
  .open-log .log-bg .head {
    padding: 30px 11% 30px 55px;
  }
  .open-log .log-label {
    padding: 20px 10px;
  }
}



