.memory-card {
  width: 380px;
  padding: 10px 10px;
  overflow: hidden;
  height: 180px;
  margin: 0 auto;
  position: relative;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.memory-card .crad-head {
  padding: 30px 20px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  height: 127px;
  background: #eb524a;
  color: white;
  font-weight: 500;
  position: relative;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
.memory-card .crad-head .card-head-img {
  display: inline-block;
  vertical-align: middle;
  margin-right: 40px;
}
.memory-card .crad-head .card-head-title {
  display: inline-block;
  margin: 0 auto;
  vertical-align: middle;
}
.memory-card .crad-head .card-head-title h2 {
  color: white;
}
.memory-card .crad-head .card-head-title p {
  text-align: right;
}
.memory-card .crad-head .card-head-button {
  width: 200px;
  overflow: hidden;
  position: absolute;
  bottom: -25px;
  height: 50px;
  color: #c63535;
  left: 90px;
  line-height: 50px;
  padding: 0 20px;
  text-align: center;
  background: #ffbe44;
  border-radius: 15px;
  z-index: 700;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.memory-card .crad-head .card-head-button:hover {
  cursor: pointer;
}
.memory-card .crad-head .card-head-button p {
  display: block;
}
.memory-card .crad-head .card-head-button i {
  display: none;
}
.memory-card .crad-head .card-head-desc {
  position: absolute;
  height: 0px;
  line-height: 50px;
  padding-left: 10px;
  left: 0;
  top: 127px;
  background: #c63535;
  width: 100%;
  color: white;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  overflow: hidden;
}
.memory-card .card-body {
  position: absolute;
  top: 175px;
  height: 0px;
  overflow: hidden;
  padding: 10px;
  left: 0;
}
.memory-card .card-body .card-body-tab {
  display: block;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  height: 76px;
  margin: 15px 0px;
  width: 360px;
  border-radius: 5px;
  background: #f8f8f8;
  color: black;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  font-size: 16px;
  line-height: 76px;
  padding-left: 20px;
  opacity: 0;
}
.memory-card .card-body .card-body-tab i {
  font-size: 30px;
  margin-right: 20px;
}
.memory-card .card-body .card-body-tab:nth-child(2) {
  margin-top: -30px;
  -webkit-transition-delay: 0.2s;
       -o-transition-delay: 0.2s;
          transition-delay: 0.2s;
  top: 70px;
}
.memory-card .card-body .card-body-tab:nth-child(3) {
  margin-top: -30px;
  top: 160px;
  -webkit-transition-delay: 0.4s;
       -o-transition-delay: 0.4s;
          transition-delay: 0.4s;
}
.memory-card.card-expand {
  height: 500px;
}
.memory-card.card-expand .crad-head .card-head-button {
  left: 270px;
  width: 50px;
}
.memory-card.card-expand .crad-head .card-head-button p {
  display: none;
}
.memory-card.card-expand .crad-head .card-head-button i {
  display: inline;
}
.memory-card.card-expand .crad-head .card-head-desc {
  height: 50px;
}
.memory-card.card-expand .card-body {
  height: auto;
}
.memory-card.card-expand .card-body .card-body-tab {
  opacity: 1;
  margin-top: 15px;
}



