#home-container {
  width: 100%;
  overflow: hidden;
  /* Default image container */
}
#home-container .banner {
  position: relative;
  width: 100%;
  height: 100vh;
  padding: 0;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
#home-container .background {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  -webkit-transform: translate3d(0, 0, 0) scale(1.25) rotate3d(1, 1, 1, 0deg);
          transform: translate3d(0, 0, 0) scale(1.25) rotate3d(1, 1, 1, 0deg);
  background: black url(../../images/home/home-bg.jpg) no-repeat center center;
  background-size: cover;
}
#home-container h1 {
  position: absolute;
  width: 100%;
  text-align: center;
  color: #EEE;
  margin: 0;
  line-height: 100vh;
  text-transform: uppercase;
  text-shadow: 0 0 .3rem black;
}
.home-log-container {
  width: 1024px;
  height: 400px;
  margin: 30px auto;
  position: relative;
}
.home-log-container .log-img {
  width: 372px;
  height: 400px;
  overflow: hidden;
  background-position: center center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}
.home-log-container .log-content {
  display: inline-block;
  height: 360px;
  width: 700px;
  position: absolute;
  left: 322px;
  top: 20px;
  background: white;
  -webkit-box-shadow: 0 10px 22px rgba(0, 0, 0, 0.2);
          box-shadow: 0 10px 22px rgba(0, 0, 0, 0.2);
  padding: 40px 50px;
  padding-left: 75px;
}
.home-log-container .log-content p {
  margin-top: 30px;
}
.home-log-container .log-content h2 {
  display: inline-block;
  font-weight: 900;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #1faafa), to(#0a46b6));
  background-image: -webkit-linear-gradient(left, #1faafa 0, #0a46b6 100%);
  background-image: -o-linear-gradient(left, #1faafa 0, #0a46b6 100%);
  background-image: linear-gradient(to right, #1faafa 0, #0a46b6 100%);
  background-position: 0 1.2em;
  background-size: 0 100%;
  background-repeat: no-repeat;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.home-log-container:nth-child(odd) .log-img {
  left: 650px;
}
.home-log-container:nth-child(odd) .log-content {
  left: 0;
  padding-left: 50px;
}
.home-log-container:hover {
  cursor: pointer;
}
.home-log-container:hover .log-img {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
.home-log-container:hover .log-content h2 {
  background-size: 100% 100%;
}
@media screen and (max-width: 1050px) {
  .home-log-container {
    width: 800px;
    height: 300px;
  }
  .home-log-container .log-img {
    width: 300px;
    height: 300px;
    left: 0;
  }
  .home-log-container .log-content {
    height: 260px;
    width: 550px;
    left: 270px;
  }
  .home-log-container:nth-child(odd) .log-img {
    left: 500px;
  }
}
@media screen and (max-width: 800px) {
  .home-log-container {
    width: 100%;
    height: auto;
  }
  .home-log-container .log-img {
    position: relative;
    width: 100%;
    top: 0;
    left: 0;
  }
  .home-log-container .log-content {
    position: relative;
    width: 100%;
    top: 0;
    left: 0;
  }
  .home-log-container:nth-child(odd) .log-img {
    left: 0;
  }
}



